export default {
	data() {
		return {
			gender:'',
			isOrNo:'是',
			items:[{name:'man',value:'男'},{name:'woman',value:'女'}],
			items1:[{name:'yes',value:'是'},{name:'no',value:'否'}],
			content:'',
			tatVal:0,
			birthDate: '2018-12-25',
			lostDate: '2019-6-25',
			feature:'',
			lostProcess:'',
			otherInfo:'',
			otherDescription:'',
			imgList: []
		}
	},
	onLoad() {

	},
	methods: {
		radioChange(e){
			this.gender=e.detail.value
		},
		radio1Change(e){
			this.isOrNo=e.detail.value
		},
		BirthDateChange(e) {
			this.birthDate = e.detail.value
		},
		LostDateChange(e) {
			this.lostDate = e.detail.value
		},
		feaInput(e) {
			this.feature = e.detail.value
		},
		lostInput(e) {
			this.lostProcess = e.detail.value
		},
		otherInfoInput(e){
			this.otherInfo = e.detail.value
		},
		otherDescriptionInput(e){
			this.otherDescription = e.detail.value
		},
		ChooseImage() {
			uni.chooseImage({
				count: 4, //默认9
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album'], //从相册选择
				success: (res) => {
					if (this.imgList.length != 0) {
						this.imgList = this.imgList.concat(res.tempFilePaths)
					} else {
						this.imgList = res.tempFilePaths
					}
				}
			});
		},
		ViewImage(e) {
			uni.previewImage({
				urls: this.imgList,
				current: e.currentTarget.dataset.url
			});
		},
		DelImg(e) {
			uni.showModal({
				title: '寻亲者',
				content: '确定要删除这张照片吗？',
				cancelText: '再看看',
				confirmText: '再见',
				success: res => {
					if (res.confirm) {
						this.imgList.splice(e.currentTarget.dataset.index, 1)
					}
				}
			})
		},
		submit(e){
			
		}
	}
}
